<template>
    <div class="setting">
        <el-card class="set-card">
            <!-- 题目 -->
            <div class="card-title">
                <el-text style=" font-size: 30px;font-weight: bold;color: black;">账号设置</el-text>
            </div>
            <!-- 分界线 -->
            <el-divider style="width:100%">
                <el-icon><star-filled /></el-icon>
            </el-divider>
            <div class="card-contain">
                <div class="contain-tip">
                    <el-popover :width="300"
                        popper-style="box-shadow: rgb(14 18 22 / 35%) 0px 10px 38px -10px, rgb(14 18 22 / 20%) 0px 10px 20px -15px; padding: 20px;">

                        <template #reference>
                            <el-avatar :src="logo" />
                        </template>
                        <template #default>
                            <div class="demo-rich-conent" style="display: flex; gap: 16px; flex-direction: column">
                                <el-avatar :size="60" :src="logo" style="margin-bottom: 8px" />
                                <div>
                                    <p class="demo-rich-content__name" style="margin: 0; font-weight: 500">
                                        星辰阁
                                    </p>
                                </div>

                                <p class="demo-rich-content__desc" style="margin: 0">
                                    本页面验证码邮箱为本用户的原邮箱<br>
                                    注：防止用户被盗号后无法找回账号
                                </p>
                            </div>
                        </template>
                    </el-popover>
                </div>

                <div class="contain-email">

                    <h2>绑定邮箱</h2>

                    <div style="margin-top: 10px;">
                        <el-form :model="setEmail">

                            <el-form-item>
                                <div><text>原邮箱 {{ email }}</text></div>
                            </el-form-item>

                            <!-- 邮箱 -->
                            <el-form-item>
                                <el-input type="text" placeholder="请输入新邮箱" :suffix-icon="Message" style="width: 300px;
                                    height: 40px;" v-model="setEmail.newEmail"></el-input>
                            </el-form-item>

                            <!-- 邮箱验证码 -->
                            <el-form-item>
                                <el-input type="text" placeholder="请输入验证码" style="width: 200px;
                                    height: 40px;" v-model="setEmail.code"></el-input><el-button
                                    style="width: 100px;height: 40px;" @click="sendEmail">发送验证码</el-button>
                            </el-form-item>

                            <el-form-item>
                                <el-button class="to-button" @click="updateEmail">
                                    保存
                                </el-button>
                            </el-form-item>

                        </el-form>
                    </div>
                </div>

                <div class="contain-password">

                    <h2>密码登录</h2>
                    <div style="margin-top: 10px;">
                        <el-form :model="setPassword">

                            <el-form-item>
                                <el-input type="password" placeholder="请输入新密码" :suffix-icon="Lock" style="width: 300px;
                                    height: 40px;" v-model="setPassword.newPassword" show-password></el-input>
                            </el-form-item>

                            <!-- 邮箱验证码 -->
                            <el-form-item>
                                <el-input type="text" placeholder="请输入验证码" style="width: 200px;
                                    height: 40px;" v-model="setPassword.code"></el-input><el-button
                                    style="width: 100px;height: 40px;" @click="sendEmail">发送验证码</el-button>
                            </el-form-item>

                            <el-form-item>
                                <el-button class="to-button" @click="updatePassword">
                                    保存
                                </el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                </div>

                <div class="contain-otherlogin">

                </div>

            </div>
        </el-card>
    </div>
</template>

<script setup>
import { StarFilled } from '@element-plus/icons-vue'
// import store from '@/store';
import { Lock, Message } from '@element-plus/icons-vue'
import { reactive, ref } from 'vue'
import { ElMessage } from 'element-plus';
import $ from 'jquery'
import logo from '@/assets/logo.png'
const verKey = ref("");

const setEmail = reactive({
    newEmail: "",
    code: ""
})
const setPassword = reactive({
    newPassword: "",
    code: ""
})

const email = localStorage.getItem("email")

const sendEmail = () => {
    $.ajax({
        url: "http://localhost:3010/User/SendEmail",
        type: "get",
        data: {
            SendTo: email
        },
        success(resp) {
            if (resp.message == "success") {
                verKey.value = resp.key;
                open("发送成功,请耐心等待~", "success");
            }
            else {
                open("发送失败~", "error");
            }
        },
        error() {
            open("发送失败", "error");
        }
    })
}

const updateEmail = () => {
    $.ajax({
        url: "http://localhost:3010/User/updateEmail",
        type: "post",
        headers: {
            Authorization: "Bearer " + localStorage.getItem("jwt_token"),
        },
        data: {
            newEmail: setEmail.newEmail,
            code: setEmail.code,
            key: verKey.value
        },
        success(resp) {
            if (resp.message == "success") {
                localStorage.setItem("email", setEmail.newEmail);
                setEmail.newEmail = "";
                setEmail.code = "";
                open("修改成功~", "sucess");
            } else {
                open(resp.message, "error");
            }
        },
        error() {
            open("修改失败~", "error");
        }

    })
}

const updatePassword = () => {
    $.ajax({
        url: "http://localhost:3010/User/updatePassword",
        type: "post",
        headers: {
            Authorization: "Bearer " + localStorage.getItem("jwt_token"),
        },
        data: {
            newPassword: setPassword.newPassword,
            code: setPassword.code,
            key: verKey.value
        },
        success(resp) {
            if (resp.message == "success") {
                setPassword.newPassword = "";
                setPassword.code = "";
                open("修改成功~", "sucess");
            } else {
                open(resp.message, "error");
            }
        },
        error() {
            open("修改失败~", "error");
        }
    })
}

const open = (now_message, color) => {
    ElMessage({
        message: now_message,
        grouping: true,
        type: color,
    })
}
</script>

<style lang="less" scoped>
.to-button {
    width: 300px;
    background-image: linear-gradient(to right, #87CEEB, #B0E0E6);
    margin-top: 20px;
    height: 40px;
}

.setting {

    // 背景颜色
    background-color: rgb(247, 248, 249);

    // 左右对齐
    display: flex;
    justify-content: center;

    // 大小
    height: 100%;
    min-height: 100vh;
    width: 100%;
    padding-top: 80px;

    .set-card {

        // 宽度
        width: 70%;

        .card-title {
            // 左右对齐
            display: flex;
            justify-content: center;
        }

        .card-contain {
            margin-top: 20px;
            display: flex;
            justify-content: center;
            flex-direction: column;
            position: relative;

            .contain-tip {
                position: absolute;
                top: 0;
            }

            .contain-email {
                margin-top: 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }

            .contain-password {
                margin-top: 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }
        }

    }
}
</style>