import {
  createRouter,
  createWebHistory
} from 'vue-router'
import HomeView from '@/views/home/HomeView.vue'
import SetView from '@/views/setting/SetView.vue'
import ProblemView from '@/views/problem/ProblemHome.vue'
const routes = [{
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/user/:account',
    name: 'user',
    props: true,
    component: () => import('@/views/user/UserView.vue'),
  },
  {
    path: '/user/',
    redirect: '/'
  },
  {
    path: '/setting',
    name: 'setting',
    component: SetView
  },
  {
    path: '/problem',
    name: 'problem',
    component: ProblemView
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router