<script setup>
import { ref, reactive, defineEmits } from 'vue'
import { User, Lock, Message } from '@element-plus/icons-vue'
import store from '@/store';
import router from '@/router';
import $ from 'jquery'
import { ElMessage, ElMessageBox } from 'element-plus';

// 操作状态
const nowstate = ref(0)
const tologin = ref(false)
const toregister = ref(false)
const verKey = ref('')
const showuser = ref(false)
const emailLogin = ref(false)

//子组件传递消息
const emit = defineEmits(['visible']);

// 隐私协议
const CheckAgreement = ref(true)

// 登录信息
const LoginData = reactive({
    account: '',
    password: '',
    code: ''
})

// 注册信息
const RegisterData = reactive({
    email: '',
    username: '',
    password: '',
    code: ''
})

// 用户信息
const UserData = reactive({
    username: '',
    account: '',
    password: ''
})

const LoginEmail = reactive({
    email: '',
    code: ''
})

const sendEmail = () => {
    if (LoginEmail.email == null || LoginEmail.email == "") {
        open("邮箱不能为空", "error");
        return;
    }
    $.ajax({
        url: "http://localhost:3010/User/SendEmail",
        type: "get",
        data: {
            SendTo: LoginEmail.email,
        },
        success(resp) {
            if (resp.message == "success") {
                verKey.value = resp.key;
            }
            else {
                open("邮箱不能为空", "error");
            }
        },
        error(resp) {
            open(resp, "error");
        }
    })
}


const loginverify = () => {
    if (CheckAgreement.value == false) {
        open("请先同意协议", "error");
        return;
    }
    if (emailLogin.value) {
        store.dispatch("loginemail", {
            email: LoginEmail.email,
            code: LoginEmail.code,
            key: verKey.value,
            success() {
                store.dispatch("getinfo", {
                    success() {
                        router.push({ name: 'home' });
                        open("登录成功", "success");
                        tologin.value = false;
                        verKey.value = "";
                        LoginData.account = "";
                        LoginData.password = "";
                        LoginData.key = "";
                        emit('visible', false);
                    },
                    error(resp) {
                        open(resp.error_message, "error");
                    }
                })
            },
            error(resp) {
                open(resp.error_message, "error");
            },
            Loginerror(resp) {
                open(resp, "error");
                tologin.value = false;
            }
        }
        )
    } else {
        LoginData.code = "";
        tologin.value = true;
        $.ajax({
            url: "http://localhost:3010/User/captcha",
            type: "get",
            data: {
                account: LoginData.account,
            },
            success(resp) {
                verKey.value = resp.key
                $('#LoginImg').attr('src', resp.image)
            }
        })
    }

}

const registerverify = () => {
    if (CheckAgreement.value == false) {
        open("请先同意协议", "error");
        return;
    }
    if (RegisterData.email == null || RegisterData.email == "") {
        open("邮箱不能为空", "error");
        return;
    }
    RegisterData.code = "";
    toregister.value = true;
    $.ajax({
        url: "http://localhost:3010/User/SendEmail",
        type: "get",
        data: {
            SendTo: RegisterData.email,
        },
        success(resp) {
            if (resp.message == "success") {
                verKey.value = resp.key;
            }
            else {
                open("邮箱不能为空", "error");
            }
        },
        error(resp) {
            open(resp, "error");
        }
    });
}

const login = () => {
    if (verKey.value == null) {
        open("验证码错误", "error");
        return;
    }
    store.dispatch("login", {
        account: LoginData.account,
        password: LoginData.password,
        code: LoginData.code,
        key: verKey.value,
        success() {
            store.dispatch("getinfo", {
                success() {
                    router.push({ name: 'home' });
                    open("登录成功", "success");
                    tologin.value = false;
                    verKey.value = "";
                    LoginData.account = "";
                    LoginData.password = "";
                    LoginData.key = "";
                    emit('visible', false);
                },
                error(resp) {
                    open(resp.error_message, "error");
                }
            })
        },
        error(resp) {
            open(resp.error_message, "error");
            loginverify();
        },
        Loginerror(resp) {
            open(resp, "error");
            tologin.value = false;
        }
    });
}

const register = () => {
    if (verKey.value == null) {
        open("验证码错误", "error");
        return;
    }
    $.ajax({
        url: "http://localhost:3010/User/account/register/",
        type: "post",
        data: {
            email: RegisterData.email,
            username: RegisterData.username,
            password: RegisterData.password,
            code: RegisterData.code,
            key: verKey.value,
        },
        success(resp) {
            if (resp.error_message == "success") {
                router.push({ name: 'home' });
                toregister.value = false;
                UserData.account = resp.account;
                UserData.username = RegisterData.username;
                UserData.password = RegisterData.password;
                RegisterData.username = "";
                RegisterData.password = "";
                RegisterData.confirmpassword = "";
                RegisterData.key = "";
                open("注册成功", "success");
                showuser.value = true;
            } else {
                registerverify();
                open(resp.error_message, "error");
            }
        }
    })
}

const open = (now_message, color) => {
    ElMessage({
        message: now_message,
        grouping: true,
        type: color,
    })
}

const handleClose = () => {
    ElMessageBox.confirm('请认真保存您的账号密码')
        .then(() => {
            showuser.value = false;
        })
        .catch(() => {
            // catch error
        })
}
</script>

