<template>
    <div class="UpperBar" id="UpperBar">

        <!-- 搭建画布 -->
        <canvas id="canvas"></canvas>

        <el-image class="carouselImg" :src="background" fit="cover"></el-image>

        <!-- 底纹波浪线 -->
        <div class="waveWrapper waveAnimation">
            <div class="waveWrapperInner bgTop">
                <div class="wave waveTop" />
            </div>
            <div class="waveWrapperInner bgMiddle">
                <div class="wave waveMiddle" />
            </div>
            <div class="waveWrapperInner bgBottom">
                <div class="wave waveBottom" />
            </div>
        </div>

        <!-- 樱花图片 -->
        <img style="display: none;" id="petals1" :src="petalsImage1">
        <img style="display: none;" id="petals2" :src="petalsImage2">

    </div>
</template>

<script setup>

import { onMounted } from 'vue';
import petalsImage1 from '@/assets/images/petals1.png'
import petalsImage2 from '@/assets/images/petals2.png'
import background from '@/assets/images/background.png'
const petals = 50
const petalsWidth = 16
const petalsHeight = 16
const petalsSpeed = 25

//添加樱花飘落
onMounted(() => {
    const content = document.getElementById('UpperBar');

    let canvas = document.getElementById('canvas')

    let img0 = document.getElementById('petals1')
    let img1 = document.getElementById('petals2')

    let context = canvas.getContext('2d');
    let w = window.innerWidth;
    let h = content.offsetHeight;
    canvas.width = w;
    canvas.height = h;
    let imgArr = [img0, img1]
    let petalArr = []
    for (let i = 0; i < petals; i++) {
        petalArr.push({
            x: Math.random() * w,
            y: Math.random() * 1,
            vx: Math.random() * 1.4,
            vy: Math.random() * 5,
            img: imgArr[parseInt(Math.random() * 2)]
        })
    }
    const draw = () => {
        context.clearRect(0, 0, w, h);
        context.beginPath()
        petalArr.forEach(e => {
            context.moveTo(e.x, e.y);
            context.drawImage(e.img, e.x, e.y, petalsWidth, petalsHeight);
        })
        context.fill();
        move()
    }
    const move = () => {
        petalArr.forEach(e => {
            e.y += e.vy
            e.x += e.vx
            if (e.y > h || e.x > w) {
                e.y = Math.random() * 1
                e.x = Math.random() * w
            }
        })
    }
    setInterval(draw, petalsSpeed)
})

</script>

<style lang="less" scoped>
.UpperBar {
    background-color: white;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 3;
        background-attachment: fixed;
    }

    @keyframes move_wave {
        0% {
            transform: translateX(0) translateZ(0) scaleY(1)
        }

        50% {
            transform: translateX(-25%) translateZ(0) scaleY(0.55)
        }

        100% {
            transform: translateX(-50%) translateZ(0) scaleY(1)
        }
    }

    #canvas {
        position: absolute;
        z-index: 98;
        top: 0;
        left: 0;

        @media (max-width:768px) {
            left: 0;
        }
    }

    .waveWrapper {
        overflow: hidden;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
    }

    .waveWrapperInner {
        position: absolute;
        width: 100%;
        overflow: hidden;
        height: 100%;
        bottom: -1px;
    }

    .bgTop {
        z-index: 15;
        opacity: 0.5;
    }

    .bgMiddle {
        z-index: 10;
        opacity: 0.75;
    }

    .bgBottom {
        z-index: 5;
    }

    .wave {
        position: absolute;
        left: 0;
        width: 200%;
        height: 100%;
        background-repeat: repeat no-repeat;
        background-position: 0 bottom;
        transform-origin: center bottom;
    }

    .waveTop {
        background-size: 50% 100px;
    }

    .waveAnimation .waveTop {
        animation: move-wave 3s;
        -webkit-animation: move-wave 3s;
        -webkit-animation-delay: 1s;
        animation-delay: 1s;
    }

    .waveMiddle {
        background-size: 50% 120px;
    }

    .waveAnimation .waveMiddle {
        animation: move_wave 10s linear infinite;
    }

    .waveBottom {
        background-size: 50% 100px;
    }

    .waveAnimation .waveBottom {
        animation: move_wave 15s linear infinite;
    }

    .waveTop {
        background-image: url('@/assets/images/wave-top.png');
    }

    .waveMiddle {
        background-image: url('@/assets/images/wave-mid.png');
    }

    .waveBottom {
        background-image: url('@/assets/images/wave-bot.png');
    }

    @-webkit-keyframes float {
        0% {
            -webkit-transform: translateY(0);
            transform: translateY(0)
        }

        50% {
            -webkit-transform: translateY(-6px);
            transform: translateY(-6px)
        }

        100% {
            -webkit-transform: translateY(0);
            transform: translateY(0)
        }
    }

    @keyframes float {
        0% {
            -webkit-transform: translateY(0);
            -ms-transform: translateY(0);
            transform: translateY(0)
        }

        50% {
            -webkit-transform: translateY(-6px);
            -ms-transform: translateY(-6px);
            transform: translateY(-6px)
        }

        100% {
            -webkit-transform: translateY(0);
            -ms-transform: translateY(0);
            transform: translateY(0)
        }
    }
}

.carouselImg {
    width: 100%;
    height: inherit;
    min-height: 740px;
    min-width: 1400px;
}


::v-deep(.el-carousel__container) {
    height: 98vh;
}
</style>
