<script setup>
import { ref } from 'vue';
import Logo from './components/NavLogo.vue';
import ModelDialog from '../ModelDialog.vue';
import Login from './components/LoginDialog.vue';
import store from '@/store';
// import router from '@/router';
import { ElMessage } from 'element-plus';
// 刷新更新数据
const is_login = localStorage.getItem("is_login");

const belogin = ref(false);

if (is_login || store.state.user.is_login) {
    belogin.value = true;
} else {
    belogin.value = false;
}

const jwt_token = localStorage.getItem("jwt_token");

if (jwt_token) {
    store.commit("updateToken", jwt_token);
    store.dispatch("getinfo", {
        success() {

        },
        error() {
            open("刷新太快~", "error");
        }
    })
}

// 登录，设置控制数据
const loginvisible = ref(false)
const settingvisible = ref(false)

// 子组件传递
const LoginEvent = (data) => {
    loginvisible.value = data;
    belogin.value = true;
}

// 退出
const logout = () => {
    store.dispatch("logout");
    belogin.value = false;
}

const open = (now_message, color) => {
    ElMessage({
        message: now_message,
        grouping: true,
        type: color,
    })
}
</script>

<style lang="less" scoped>
// 下拉导航栏
.dropdown {
    width: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.navbar {

    // 长高占比
    width: 100%;
    height: 80px;

    position: absolute;

    top: 0;

    z-index: 999;

    .navbar-content {

        width: 100%;
        height: 80px;

        &:hover {
            background-color: rgba(255, 255, 255, 0.5);
        }

        // 两侧对齐
        display: flex;
        justify-content: space-between;

        // 上下居中
        align-content: center;

        // 导航栏左侧样式
        .content-left {

            // 上下居中
            display: flex;
            align-items: center;

            // 导航栏超链接
            .left-box {

                // 上下居中
                display: flex;
                align-items: center;

                // 超链接单项
                .left-item {

                    // 左间距，字体颜色去除下划线
                    margin-left: 30px;
                    color: #666666;

                    // 超链接下划线初始化
                    .navItem::after {

                        // 下滑内容
                        content: "";

                        display: block;

                        position: absolute;

                        // 下划线高度
                        height: 3px;

                        // 下划线颜色
                        background-color: #0099FF;

                        // 下划线宽度
                        width: 0;

                        // 动画时间
                        transition: width .25s ease-in-out;
                    }

                    // 鼠标放置样式

                    // 字体颜色
                    &:hover .navItem {
                        color: black;
                    }

                    // 下划线颜色
                    &:hover .navItem:after {
                        width: 30px;
                    }
                }

            }
        }

        // 导航栏右侧样式
        .content-right {

            // 上下居中
            display: flex;
            align-items: center;

            // 用户头像
            .user-avatar {
                margin-right: 20px;
            }

            // 登录按钮
            .login-button {
                // 渐变颜色
                background-image: linear-gradient(to right, #87CEEB, #B0E0E6);

                // 右间距
                margin-right: 20px;
            }

            // 设置按钮
            .setting {

                // 设置图标大小
                width: 32px;
                height: 32px;

                // 右间距
                margin-right: 20px;

                // 设置图标旋转动画
                animation: rotateAnimation 2s infinite linear;
            }

            /* 设置旋转关键帧 */
            @keyframes rotateAnimation {

                0% {
                    transform: rotate(0deg);
                }

                100% {
                    transform: rotate(360deg);
                }
            }
        }

        .min-right {
            display: none;
        }
    }

}


// 小窗口样式
@media (max-width: 992px) {

    // 导航栏
    .navbar {

        // 高度
        height: 50px;

        // 内容区域
        .navbar-content {

            margin-top: 0;

            justify-content: center;
            align-items: center;

            // 隐藏
            .content-left {
                .left-box {
                    display: none;
                }
            }

            .content-right {
                display: none;
            }

            // 显示更多
            .min-right {

                // 显示
                display: block;

                // 位置信息
                position: absolute;

                // 右边边距
                right: 10px;
            }
        }
    }
}
