import router from '@/router';
import $ from 'jquery'
export default {
    state: {
        id: "",
        account: "",
        username: "",
        photo: "",
        token: "",
        email: "",
        is_login: false,
    },
    getters: {

    },
    mutations: {
        updateUser(state, user) {
            state.id = user.id;
            state.account = user.account;
            state.username = user.username;
            state.photo = user.photo;
            state.is_login = user.is_login;
            state.email = user.email;
        },
        updateToken(state, token) {
            state.token = token;
        },
        logout(state) {
            state.id = "";
            state.account = "";
            state.username = "";
            state.photo = "";
            state.token = "";
            state.email = "";
            state.is_login = false;
        }
    },
    actions: {
        loginemail(context, data) {
            $.ajax({
                url: "http://localhost:3010/User/account/email",
                type: "post",
                data: {
                    email: data.email,
                    code: data.code,
                    key: data.key
                },
                success(resp) {
                    if (resp.error_message === "success") {
                        localStorage.setItem("jwt_token", resp.token);
                        localStorage.setItem("is_login", true);
                        localStorage.setItem("account", resp.account);
                        localStorage.setItem("email", data.email);
                        context.commit("updateToken", resp.token);
                        data.success(resp)
                    } else {
                        data.error(resp);
                    }
                },
                error() {
                    data.Loginerror("账号或验证码错误");
                }
            })
        },
        login(context, data) {
            $.ajax({
                url: "http://localhost:3010/User/account/token/",
                type: "post",
                data: {
                    account: data.account,
                    password: data.password,
                    code: data.code,
                    key: data.key
                },
                success(resp) {
                    if (resp.error_message === "success") {
                        localStorage.setItem("jwt_token", resp.token);
                        localStorage.setItem("is_login", true);
                        localStorage.setItem("account", data.account);
                        localStorage.setItem("email", data.email);
                        context.commit("updateToken", resp.token);
                        data.success(resp)
                    } else {
                        data.error(resp);
                    }
                },
                error() {
                    data.Loginerror("账号或密码错误");
                }
            })
        },
        getinfo(context, data) {
            $.ajax({
                url: "http://localhost:3010/User/account/info/",
                type: "post",
                headers: {
                    Authorization: "Bearer " + context.state.token,
                },
                success(resp) {
                    if (resp.error_message === "success") {
                        context.commit("updateUser", {
                            ...resp,
                            is_login: true,
                        });
                        data.success(resp);
                    } else {
                        data.error(resp);
                    }
                },
                error(resp) {
                    data.error(resp);
                }
            })
        },
        logout(context) {
            localStorage.removeItem("jwt_token");
            localStorage.removeItem("is_login");
            localStorage.removeItem("account");
            localStorage.removeItem("email");
            router.push({
                name: 'home'
            });
            context.commit("logout");
        }
    }
}